header {
    position: sticky;
    background: var(--gray);
    padding: 12px 0;
    box-shadow: 0 4px 10px #0000008c;
    transition: all .3s ease-in-out;
    z-index: 999;
    top: 0;

}

nav  a , .Header_menu__2IO3w li{
    font: 16px 'HelveticaR';
    color: #fff;
    cursor: pointer;
    transition: all .3s ease-in-out;
    &:hover {
        color: #b31313;
        text-shadow: 0 0 10px var(--red);
    }
}




nav ul,
.Header_right__3cn7n ul,
.Header_menu__2IO3w,
header > div {
    display: flex;
    align-items: center;
}
.Header_menu__2IO3w{
    will-change: transform, box-shadow;
    & a{
        padding: 6px 0;
    } 
}
.Header_right__3cn7n{
    flex: 1 1;
}
header > div {
    justify-content: space-between;
}

nav li {
    margin: 0 30px 0 0;
}

.Header_logo__1S5wr {
    margin: 0 30px 0 0;
}
.Header_right__3cn7n nav{
    margin: 0 auto 0 0;
}
.Header_header__tools__aZvWx li {
    margin: 0 26px 0 0;
}

nav li:last-child {
    margin: 0;
}


.Header_submenu__2LWRE{
    position: relative;
    & ul{
        position: absolute;
        top: 100%;
        max-height: 0;
        left: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: start;
        background: var(--gray);
        padding: 0;
        width: max-content;
        border-radius: 10px;
        transition: all .3s ease-in-out;
        overflow: hidden;
        & li{
            margin: 0 0 6px 0;
            padding: 0;
            &:last-child{
                margin: 0;
            }
        }
    }
    &:hover ul{
        max-height: 200px;
        padding: 14px 11px;
        box-shadow: 0 0 5px 2px #7f7f7f ;


    }
}

.Header_header__tools__aZvWx li:last-child {
    margin: 0 20px 0 0;
}

.Header_header__tools__aZvWx {
    border-right: 2px solid var(--red);
    padding: 4px 0;
    margin: 0 20px 0 0;
}


.Header_header__tools__aZvWx {
    & li{
        padding: 0;
    }
    & a {
        display: flex;
        align-items: center;
        font: 14px 'HelveticaR';
        color: #fff;
        padding: 6px 14px;
        background: var(--red);
        border-radius: 50px;
        box-shadow: 0 4px 4px #00000024;
    
        & svg {
            margin: 0 5px 0 0;
        }
    
        &:hover path, &.Header_active__1If6O path {
            fill: #fff
        }
    
        &:hover, &.Header_active__1If6O {
            background: var(--lightRed);
        }
    }
}

.Header_lang__A9jMZ {
    font: 14px HelveticaB;
    color: #fff;
}
.Header_burger__3oYpq{
    display: none;
}

.Header_burger__btn__2L3xB {
    width: 30px;
    height: 30px;
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.Header_stick__3zxvU {
    width: 30px;
    box-sizing: border-box;
    height: 3px;
    background: #fff;
    border: 0;
    border-radius: 10px;
    position: absolute;
    left: 0;
    top: 0;
    transition: all .3s ease-in-out;
    margin: 0;
    transform-origin: center center;
}

.Header_active__burger__2aLcD .Header_stick__3zxvU:first-child {
    animation-name: Header_firstStick__35ADO;
    animation-delay: 1.2s;
    animation-duration: .9s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
    top: 0;
}

.Header_stick__3zxvU:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
}

.Header_stick__3zxvU:last-child {
    bottom: 0;
    top: initial;
    animation-delay: .75s;
    animation-duration: .9s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: both;
}




.Header_active__burger__2aLcD .Header_stick__3zxvU:nth-child(2){
    width: 3px;
    transition: width .4s ease-in-out;
    animation-name: Header_ball__3E_CK;
    animation-duration: .9s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;
    animation-delay: .4s;
}

.Header_active__burger__2aLcD .Header_stick__3zxvU:last-child{
    animation-name: Header_lastStick__1EZ4e;
}


.Header_deactive__burger__sQIVk  .Header_stick__3zxvU:first-child{
    animation-name: Header_deactiveOne__3dYt5;
    animation-delay: 0ms;
    animation-duration: 500ms;
}
.Header_deactive__burger__sQIVk  .Header_stick__3zxvU:last-child{
    animation-name: Header_deactiveTwo__OACUp;
    animation-delay: 0ms;
    animation-duration: 500ms;
}

@keyframes Header_deactiveOne__3dYt5 {
    0%{
        top: 50%;
        transform: rotate(-45deg) translateY(-50%);
    }
    100%{
        top: 0;
        transform: rotate(0) translateY(0);
    }
}

@keyframes Header_deactiveTwo__OACUp {
    0%{
        bottom: 50%;
        transform: rotate(45deg) translateY(50%);
    }
    100%{
        bottom: 0;
        transform: rotate(0) translateY(0);
    }
}



@keyframes Header_ball__3E_CK {
    0%{
        top: 50%;
    }
    20%{
        top: 100%;
    }
    50%{
        top: 0;
    }
    100%{
        top: 110%;
    }
}

@keyframes Header_firstStick__35ADO {
    0%{
        top:  0;
    }
    20%{
        top: 50%;
    }
    50%{
        transform: rotate(-56deg) translateY(-50%);
    }
    80%{
        transform: rotate(-36deg) translateY(-50%);
    }
  

    100%{
        top: 50%;
        transform: rotate(-45deg) translateY(-50%);
        box-shadow: 0 0 3px 1px var(--red);
    }
}

@keyframes Header_lastStick__1EZ4e {
    0%{
        bottom: 0;
    }
    20%{
        bottom: 50%;
    }
    50%{
        transform: rotate(56deg) translateY(50%);
    }
    80%{
        transform: rotate(36deg) translateY(50%);
    }

    100%{
        bottom: 50%;
        transform: rotate(45deg) translateY(50%) ;
        box-shadow: 0 0 3px 1px var(--red);

    }
}



@media only screen and (max-width: 1000px) {
  
    .Header_burger__3oYpq{
        display: block;
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 5;
    }
    .Header_right__3cn7n{
        position: absolute;
        width: 100%;
    }
    .Header_logo__1S5wr{
        margin: 0;
        & a{
            font-size: 46px;
        }
    }
    header > div{
        justify-content: center;
    }
  
    .Header_menu__2IO3w{
        width: 100%;
        left: 0;
        top: 0;
        transform: translateX(-100%);
        opacity: 0;
        position: fixed;
        flex-direction: column;
        transition: .2s opacity .2s ease-in-out, .3s trasnform .2s ease-in-out;
        background: #303030a2;
        height: 100%;
        align-items: start;
        padding: 30px;
        z-index: -1;
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        overflow: auto;
        & nav{
            margin: 0;
            & ul{
                flex-direction: column;
                align-items: start;
            }
            & li{
                margin: 0 0 16px 0;
            }
        }
    }
    .Header_active__burger__2aLcD .Header_menu__2IO3w{
        opacity: 1;
        z-index: 1;
        box-shadow: 0 0 10px 4px var(--gray);
        transform: translateX(0);
    }

    ul.Header_header__tools__aZvWx{
        flex-direction: column;
        border:0 ;
        align-items: start;
        margin: 0 0 30px 0;
        & li{
            margin: 0 0 15px 0 ;
        }
        & li:last-child{
            margin: 0;
        }
    }

    
.Header_submenu__2LWRE{
    position: relative;
    & ul{
        position: static;
        background: none;
    }
    &:hover ul{
        max-height: 500px;
        box-shadow: none ;
    }
}
    
}