.CartSlider_cart__slider__2pG_f{
    margin: clamp(20px, 7vw, 50px) 0 0 0;
}

.CartSlider_slider__item__1bcUf{
    position: relative;
    overflow: hidden;
    border-radius: 30px;
    & h4 , & a{
        position: absolute;
        transition: all 1.2s ease-in-out;
    }
    & h4{
        color: #fff;
        font: clamp(16px, 2vw, 32px) HelveticaB;
        bottom: 0;
        left: 0;
        padding: 10px clamp(5px, 3vw, 30px) clamp(10px, 3vw, 27px) clamp(25px, 3vw, 30px);
        width: 100%;
        background-color: #3030308d;
    }
    & a{
        width: clamp(36px,4vw,56px);
        height: clamp(37px,4vw,57px);
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--red);
        right: 20px;
        top: 20px;
        border-radius: 50%;
        scale: clamp(0.7, 5vw, 1);
        & svg{
            width: clamp(15px, 4vw, 20px);
            height: clamp(15px, 4vw, 20px);
        }
        &:hover{
            box-shadow: 0 0 6px 2px var(--red);
        }
    }
}

.CartSlider_slider__arrow__3Pvbl{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 18px;
    z-index: 5;
   
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: var(--gray);
    color: #fff;
    opacity: .7;
}
.CartSlider_slider__arrow__3Pvbl:last-of-type{
    left: initial;
    right: 0;
    transform: rotate(180deg) translateY(50%);

}
